require('../style/index.sass');
require('../html/index.pug');
require('./runModule.js')

var setInitialsettings = require('./startModule').setInitialsettings;
var splash = document.getElementById('splash');

var gameState = {
	settings: {
		density: 4,
		hiding: 5,
		time: 60
	},
	buffer: undefined,
	play: false,
	result : undefined
};

window.addEventListener('load', function(e){
	setInitialsettings(gameState.settings);
		setTimeout(function(){
		hideSplash();
	}, 11000)
});

function hideSplash(){
	splash.style.opacity = 0;
	setTimeout(function(){
		splash.remove();
	}, 1000);
}

document.getElementById('close').addEventListener('click', function(){
	hideSplash();
});

module.exports.gameState = gameState;